import React from 'react';
import '../Style/Portifolio.css'
import { useEffect } from 'react';
import { useState } from 'react';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
const Portifolio = () =>{
    const [dadoVideos,setDadoVideos] = useState(null);
    const [dadosImg, setImg] = useState(null); 
    const [dados360, setdados360] = useState(null); 

    const [idVideo, setidVideo] = useState(0);
    const [idImg, setIdImg] = useState(0);
    const [id360, setId360] = useState(0);
    const [tamanhoSeta] = useState(30)
    useEffect(() => {    
          
        setDadoVideos([
            'https://www.youtube.com/embed/NCbn4ATvtDQ',
            'https://www.youtube.com/embed/M7KC0RBF3ik',
            'https://www.youtube.com/embed/abG8Rpt9_Dk',
            'https://www.youtube.com/embed/VGk1RnIhoT8',
            'https://www.youtube.com/embed/larM8-yZYbw',
            'https://www.youtube.com/embed/7LSylHorKAk',
            'https://www.youtube.com/embed/f298ZTZgHiA',
            'https://www.youtube.com/embed/7_peP3YcVcI',                
        ])
        setdados360([
            '646bf2130a25489681284dc98ec947e9',  
            '6757b186c0fe44838ef275c12594b41a',
            '6aa7ee3fcf754e0ea5a19ee88a6dd51b',
            'e3b81faf00f041b7ac88cdda2f25b460',

                     
        ])       
        let list = []
        for(let i = 1;i<=88;i++){
            let caminho = ''
            if(i>9){caminho = {id:`img${i}`, url:`./img/normal/img${i}.jpg`};}
            else{caminho = {id:`img0${i}`, url:`./img/normal/img0${i}.jpg`};}    
            list.push(caminho)
        }
        setImg(list)
    }, []);
    const handleLeftArrowImage = (e) =>{
        let a = idImg + e
        if(a<0){a=0}
        setIdImg(a)
    }
    const handleRightArrowImage = (e) =>{
        let a = idImg + e
        if(a>dadosImg.length-1){a=dadosImg.length-1}
        setIdImg(a)
    }
    const handleLeftArrowVideo = (e) =>{
        let a = idVideo + e
        if(a<0){a=0}
        setidVideo(a)
    }
    const handleRightArrowVideo = (e) =>{
        let a = idVideo + e
        if(a>dadoVideos.length-1){a=dadoVideos.length-1}
        setidVideo(a)
    }
    const handleLeftArrow360 = (e) =>{
        let a = idVideo + e
        if(a<0){a=0}
        setId360(a)
    }
    const handleRightArrow360 = (e) =>{
        let a = idVideo + e
        if(a>dadoVideos.length-1){a=dadoVideos.length-1}
        setId360(a)
    }
    return(
        <div className='Protifolio'>
            <div className='card--conteriner'>
                <div className='card'>
                    <h2>Imagens fotorealistas</h2>
                </div>
                <div className='card'>              
                    {dadosImg && <img src={dadosImg[idImg].url} alt={dadosImg[idImg].id}/>}       
                    <div className='card--seta--left' onClick={()=>{handleLeftArrowImage(-1)}}>
                    <ArrowBackIosIcon style={{fontSize:tamanhoSeta,margin:'0px 6px'}} />
                    </div>
                    <div className='card--seta--right' onClick={()=>{handleRightArrowImage(1)}}>
                    <ArrowForwardIosIcon style={{fontSize:tamanhoSeta}} />
                    </div>        
                </div>                
            </div>
            <div className='card--conteriner'>
                <div className='card'>
                    <h2>Videos</h2>
                </div>
                <div className='card'>           
                   
                    {dadoVideos && 
                    <iframe src={dadoVideos[idVideo]+'?autoplay=0&controls=0&rel=0&mute=0&showinfo=1&modestbranding=0'} key={dadoVideos[idVideo]} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope" allowFullScreen></iframe>
                    }
                    <div className='card--seta--left'  onClick={()=>{handleLeftArrowVideo(-1)}}>
                    <ArrowBackIosIcon style={{fontSize:tamanhoSeta,margin:'0px 6px'}} />
                    </div>
                    <div className='card--seta--right' onClick={()=>{handleRightArrowVideo(1)}}>
                    <ArrowForwardIosIcon style={{fontSize:tamanhoSeta}} />
                    </div>
                </div>                
            </div>
            <div className='card--conteriner'>
                <div className='card'>
                    <h2>Imagens 360º</h2>
                </div>                
                <div className='card'>  
                
                   {dados360 && <iframe title='360' className='iframe360' sandbox="allow-scripts allow-popups allow-forms allow-same-origin allow-popups-to-escape-sandbox allow-downloads" frameBorder="0" scrolling='no' src={`https://momento360.com/e/u/${dados360[id360]}?wheel=false&display-mode=clean&pan-speed=0.02&utm_campaign=embed&utm_source=other&heading=39.93&pitch=-6.71&field-of-view=82.5&size=medium`} allowFullScreen>
                    </iframe>} 
                    <div className='card--seta--left' onClick={()=>{handleLeftArrow360(-1)}}>
                <ArrowBackIosIcon style={{fontSize:tamanhoSeta,margin:'0px 6px'}} />
                </div>
                <div className='card--seta--right'  onClick={()=>{handleRightArrow360(1)}}>
                <ArrowForwardIosIcon style={{fontSize:tamanhoSeta}} />
                </div>
                </div>                
                
            </div>
        </div>        
        )        
    }
    export default Portifolio;